.App {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif
}

.App nav {
  width: 100%;
  height: 50px;
  background-color: black;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
}

.App nav a {
  margin: 20px;
  position: relative;
  top: 15px;
  color: white;
  text-decoration: none;
}

.App nav a:hover {
  color: grey;
}

.App nav a.active {
  text-decoration: underline;
}

.Entries {
  margin: 20px;
  position: relative;
  top: 50px;
}

input[type='text'], select, input[type='number'] {
  width: 98%;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  border: 1px solid black;
  padding: 10px;
}

th, th.column-amount {
  text-align: center;
}

.column-date {
  width: 15%;
  text-align: center
}

.column-description {
  width: 35%;
}

.column-header {
  width: 30%
}

.column-amount {
  width: 15%;
  text-align: right;
}

.in {
  background-color: rgb(57, 180, 57);
  color: white;
}

.out {
  background-color: rgb(153, 39, 39);
  color: white;
}

.column-remove {
  width: 5%;
  text-align: center;
}

button {
  color: white;
  background-color: black;
}

button:hover {
  color: grey;
}

.Analysis {
  margin: 20px;
  position: relative;
  top: 50px;
}

.Analysis input[type='date'] {
  margin: 10px;
}

.Analysis .pos {
  color: green;
}

.Analysis .neg {
  color: red;
}

.Analysis table {
  width: 50%;
}

.Analysis .column-header {
  width: 50%;
}

.Analysis .column-amount {
  width: 20%;
}

.Analysis .column-percentage {
  width: 20%;
  text-align: center;
}

.Analysis .column-remove {
  width: 10%;
  text-align: center;
}